.main {
  // background-color: #121d33;
  background: #0f0c29; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #24243e,
    #302b63,
    #0f0c29
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  padding: 2% 14%;

  height: auto;
  transition: 1s all;
  h1 {
    font-size: 1rem;
    color: #bfbfbf;
    margin-top: 20px;
  }

  h2 {
    font-size: 2rem;
    color: var(--purpletheme);
    margin-bottom: 40px;
    margin-top: 10px;
  }

  .left {
    .line {
      height: 2px;
      width: 40px;
      background-color: var(--purpletheme);
      margin-bottom: 10px;
    }

    h1 {
      font-size: 3.4rem;
      color: white;

      font-weight: 700;
      margin: 8px 0;
      text-transform: uppercase;
    }
    h2 {
      font-size: 1.5rem;
      color: #d6d6d6;
      font-weight: 500;
      margin-top: 20px;
      margin: 0;
      margin-top: 30px;
    }
  }
}

//

@media only screen and (max-width: 600px) {
  .main {
    // background: url(../../assets/backgrounds/mainbg.svg);
    width: 100%;
    background-size: cover;
    padding: 5% 5%;

    h1 {
      font-size: 1rem;
      color: #bfbfbf;
      margin-top: 20px;
      text-align: left;
    }

    h2 {
      text-align: left;
      font-size: 2rem;
      color: var(--purpletheme);
      margin-bottom: 40px;
      margin-top: 10px;
    }

    .left {
      .line {
        height: 2px;
        width: 40px;
        background-color: var(--purpletheme);
        margin-bottom: 10px;
        // margin: auto;
      }

      h1 {
        font-size: 2.2rem;
        color: white;
        font-weight: 700;
        margin: 8px 0;
        text-align: left;
      }
      h2 {
        font-size: 1.2rem;
        color: #d6d6d6;
        font-weight: 500;
        margin-top: 20px;
        margin: 0;
        margin-top: 30px;
        text-align: left;
      }
    }
  }
}
//
.line {
  height: 2px;
  width: 40px;
  background-color: var(--purpletheme);
  margin-bottom: 10px;
}
